.single-country {
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  padding-bottom: 8px;
  &-flag {
    img {
      width: 24px;
    }
    margin-right: 10px;
  }
  &-name {
    font-size: 14px;
    color: $second-color;
    &:hover {
      color: $second-color;
    }
  }
  a:hover {
    text-decoration: none;
  }

  &-place {
    font-weight: bold;
    font-size: 24px;
    color: $muted-color;
  }
  &-description {
    p {
      font-weight: 500;
      font-size: 18px;
      line-height: 160%;
      color: $second-color;
      margin-bottom: 2.5rem;
    }
  }
  &-brief {
    p {
      font-size: 18px;
      line-height: 22px;
      color: $second-color;
      margin-bottom: 0.5rem;
    }
  }
}