.filter-time-range {
  margin-bottom: 2rem;

  p {
    font-size: 12px;
    font-weight: 500;
  }
  .noUi-horizontal {
    height: 10px;

    .noUi-tooltip {
      bottom: 20%;
      font-weight: 500;
      font-size: 9px;
      background: #333333;
      color: white;
    }
  }
  .noUi-handle {
    background: transparent;
    border: none;
    box-shadow: none;
    &:before, &:after {
      display: none;
    }
    &[data-handle="0"]{
      .noUi-tooltip:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-bottom: 12px solid transparent;
        border-top: 11px solid transparent;
        border-left: 13px solid #333333;
        right: -12px;
        top: 0;
        clear: both;
      }
    }
    &[data-handle="1"]{
      .noUi-tooltip:before {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-bottom: 12px solid transparent;
        border-top: 11px solid transparent;
        border-right: 13px solid #333333;
        left: -12px;
        top: 0;
        clear: both;
      }
    }
  }
  .noUi-connect {
    background: $main-color;
  }
  .noUi-pips {
    .noUi-value {
      font-size: 9px;
      color: rgba(0, 0, 0, 0.2);
    }
    .noUi-marker {
      width: 1px;
      height: 20px;
      top: -15px;
    }
  }
}
