.starpicker {
  display: inline;
  @include media-breakpoint-up(sm){
    float: right;
  }
  &-item {
    cursor: pointer;
  }

  &:not(:hover){
    .starpicker-item {
      &.active {
        svg {
          path {
            fill: $main-color;
            fill-opacity: 1;
          }
        }
      }
    }
  }

  &:hover {
    .starpicker-item {
      &.hover {
        svg {
          path {
            fill: $main-color;
            fill-opacity: 1;
          }
        }
      }
    }
  }

}