.quotes {
  &-raise {
    margin-top: -1rem;
    &.bg-gray {
      background: #F6F5F7;
    }
    @include media-breakpoint-up(md){
      margin-top: -15rem;
      min-height: 15rem;
    }
  }
  &-card {
    min-height: 15rem;
    border: 1px solid #C9C8CA;
    border-radius: 8px;
    padding: 4rem 10rem;
    @include media-breakpoint-down(md){
      padding: 0;
    }
  }
  &-icon {
    position: absolute;
    top: 3rem;
    left: -1rem;
    @include media-breakpoint-down(md){
      zoom:.5;
    }
  }
  &-title {
    &-name {
      font-weight: bold;
      font-size: 24px;
      line-height: 140%;
      color: #000;
    }
    &-hotel {
      font-family: Oswald, sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      color: rgba(0, 0, 0, 0.8);
    }
    @include media-breakpoint-down(md){
      padding: 3rem 0 0 3rem;
    }
  }
  &-desc {
    font-weight: 500;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
    @include media-breakpoint-down(md){
      padding-left: 3rem;
    }
  }
  &-stars {
    @include media-breakpoint-down(md){
      padding-left: 3rem;
    }
  }
  &-review {
    font-size: 16px;
    line-height: 160%;
    color: #000;

  }

  &-card-single {
    padding: 0 4rem;
    min-height: 0;
    @include media-breakpoint-down(md){
      padding: 0;
    }

    .quotes {
      &-icon {
        left: 1rem;
        top: -1rem;
        width: 75px;
        @include media-breakpoint-down(md){
          left: -0.5rem;
          top: 1rem;
          width: 85px;
        }
      }
      &-title {
        @include media-breakpoint-down(md){
          padding: 0 1.5rem;
        }
        &-hotel {
          margin-left: 1rem;
          @include media-breakpoint-down(md) {
            display: block;
            line-height: normal;
            margin-left: 0;
          }
        }
        &-stars {
          position: absolute;
          right: 0;
          top: 8px;
        }
      }
      &-desc, &-stars {
        @include media-breakpoint-down(md){
          padding-left: 1.5rem;
        }
      }
    }
  }
}


