.ticket {
  background: #FFFFFF;
  border: 1px solid #F2584D;
  border-radius: 2px;

  &-logo {
    display: flex;
    position: absolute;
    top: 20px;
    left: 25px;
    align-items: center;
    span {
      color: $main-color;
      font-weight: bold;
      margin-left: 4px;
    }
  }

  &-price {
    &-mini, &-btn {
      position: absolute;
      top: 20px;
      right: 25px;
      transition: opacity .5s;
    }
    &-mini {
      opacity: 1;
      .btn {
        padding: 7px;
      }
    }
    &-btn {
      opacity: 0;
      .btn {
        position: absolute;
        right: -1.5rem;
        padding: 10px 20px;
        top: -0.5rem;
      }
    }
    a {
      color: $main-color;
      font-weight: bold;
      position: relative;
      &:hover {
        color: $main-color;
        text-decoration: none;
      }
    }
  }
  &:hover {
    .ticket-price {
      &-mini {
        opacity: 0;
      }
      &-btn {
        opacity: 1;
      }
    }
  }

  &-more {
    position: absolute;
    bottom: -10px;
    background: white;
    right: 30px;
    padding: 0 15px;
    z-index: 10;
    a {
      color: $second-color;
      font-weight: 500;
      font-size: 14px;
      &:hover {
        text-decoration: none;
      }
    }
    &-close {
      img {
        transform: rotate(180deg);
      }
    }
  }

  &-from, &-to {
    display: flex;
    padding-top: 2rem;
  }

  &-city {
    padding-left: 1rem;
    &-port, &-country {
      color: $muted-color;
      font-size: 14px;
    }
    &-name {
      color: $second-color;
      font-weight: bold;
      font-size: 24px;
      img {
        position: absolute;
        left: 0;
      }
    }
  }

  &-departure, &-arrival {
    &-title {
      font-weight: 500;
      font-size: 12px;
      color: $muted-color;
    }
    &-date {
      font-size: 14px;
      color: $muted-color;
    }
    &-time {
      font-weight: bold;
      font-size: 24px;
      color: $second-color;
    }
  }

  &-way {
    &-duration {
      font-weight: bold;
      font-size: 12px;
      color: $muted-color;
      margin-bottom: 0.6rem;
    }
    &-transfer {
      font-weight: 500;
      font-size: 12px;
      color: $muted-color;
      &-badge {
        position: absolute;
        top: 20%;
        left: 28%;
      }
    }
    &-mid {
      position: relative;
    }
  }

  &-details {
    display: flex;
    justify-content: space-between;
  }

  &-transfer {
    margin-top: -1rem;
    padding-bottom: 1rem;
    &-block {
      display: flex;
      align-items: baseline;
      justify-content: center;
    }

    &-to {
      top: 1.3rem;
      position: relative;
    }

    &-from {
      bottom: 0.1rem;
      position: relative;
    }

    &-duration {
      font-weight: 500;
      font-size: 12px;
      color: $muted-color;
      margin-bottom: -0.5rem;
    }
  }

  &-way-transfer-badge, &-transfer-badge, &-m-transfer-badge {
    background: #FFFFFF;
    border: 1px solid #333333;
    border-radius: 4px;
    font-weight: 500;
    font-size: 9px;
    color: #333;
    padding: 0 4px;
  }

  @include media-breakpoint-down(md){
    &-from, &-to {
      padding-top: 1.9rem;
    }

    &-m-side-arrow {
      position: absolute;
      left: -17px;
      bottom: 0;
    }

    &-m-mid {
      position: relative;
      height: 64px;
    }

    &-m-transfer {
      &-duration {
        font-weight: bold;
        font-size: 12px;
        color: $muted-color;
      }
      &-badge {
        width: fit-content;
      }
      &-text {
        font-weight: 500;
        font-size: 12px;
        color: $muted-color;
      }
    }

    &-city {
      padding-left: 0.8rem;
      &-name {
        line-height: 24px;
      }
    }

    &-departure-air, &-arrival-pin {
      position: absolute;
      text-align: center;
      left: -1.5rem;
      width: 22px;
      img {
        position: relative;
        top: -0.3rem;
      }
    }
  }

  // Toggle
  .ticket-price-detailed,
  .ticket-content-detailed,
  .ticket-more-close {
    display: none;
  }
  &.opened {
    .ticket-price-detailed,
    .ticket-content-detailed,
    .ticket-more-close {
      display: initial;
    }

    .ticket-price-compact,
    .ticket-content-compact,
    .ticket-more-open {
      display: none;
    }
  }
  // End toggle

  .posa {
    @include posa;
  }
}