.select2-container--bootstrap4 {
  display: block;
  width: 100% !important;
  background: url(/assets/img/icons/dropdown.svg) no-repeat scroll 95% 50%;
  margin-bottom: 15px!important;
  *:focus {
    outline: none;
  }
  .select2-selection {
    background: transparent;
    min-height: 48px !important;
    /*height: 48px;*/
    display: flex;
    border-radius: 4px !important;
    outline: none;
    border: 2px solid #C9C8CA !important;
    &__rendered {
      height: 100%;
      display: block !important;
      min-height: 48px;
      padding-top: 5px !important;
    }
    &__choice {
      padding: 3px;
      padding-left: 5px;
      font-weight: 500;
      font-size: 14px;
      color: #FFF!important;
      background: linear-gradient(0deg, #F2584D, #F2584D), #FF6900;
      border-radius: 4px;
      border: none;
      &__remove {
        float: right;
        color: transparent!important;
        background: url(/assets/img/icons/close.svg) no-repeat scroll 50% 50%;
      }
    }
  }
  .select2-search {
    margin-top: 3px;
    input {
      background: transparent;
    }
    &__field {
      padding-top: .15rem!important;
    }
  }
  .select2-selection__arrow b {
    display: none;
  }

}