.tag {
  cursor: pointer;
  float: left;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 3px;
  padding-left: 5px;
  font-weight: 500;
  font-size: 14px;
  color: #000;
  background: #F6F5F7;
  &.block {
    float: none;
    width: fit-content;
  }
  .tag-check, .tag-close {
    margin: 0 3px;
  }
  .tag-close {
    display: none;
  }
  &.active {
    background: linear-gradient(0deg, #F2584D, #F2584D),#FF6900;
    color: #fff;
    .tag-close {
      display: initial;
    }
    .tag-check {
      display: none;
    }
  }
  border-radius: 4px;
  border: none;
}