.hotel-single {
  &-place {
    font-weight: bold;
    font-size: 24px;
    color: $muted-color;
  }
  &-actions {
    .bookmark-link {
      img {
        position: relative;
        top: -2px;
      }
      .active {
        display: none;
      }
      &.active {
        .active {
          display: initial;
        }
        .inactive {
          display: none;
        }
      }
    }
    a {
      font-weight: bold;
      font-size: 14px;
      color: $second-color;
      @include media-breakpoint-down(md){
        font-size: 0;
        margin: 0!important;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }

  &-description {
    p {
      font-weight: 500;
      font-size: 18px;
      line-height: 160%;
      color: $second-color;
      margin-bottom: 2.5rem;
    }
  }
  &-brief {
    p {
      font-size: 18px;
      line-height: 22px;
      color: $second-color;
      margin-bottom: 0.5rem;
    }
  }
  &-apartment {
    font-weight: bold;
    font-size: 18px;
    color: $main-color;
    margin-top: 16px;
    margin-bottom: 0;
  }
}