/**
 * Document defaults (html, body)
 */

html {
  font-family: $font-family;
  font-size: $font-size-base;
  color: $color-text;
  line-height: $line-height-base;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}


html, body {
  max-width: 100%;
  overflow-x: hidden;
}

hr {
  width: 100%;
  border-color: #C9C8CA;
  border-top: 0;
}