.radio {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  color: $muted-color;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    &:checked ~ .checkmark {
      background-color: #F2584D;
      border: 2px solid #F2584D;
      &:after {
        display: block;
      }
    }
  }

  .checkmark {
    position: absolute;
    top: -4px;
    left: 0;
    width: 24px;
    height: 24px;
    background-color: white;
    border: 2px solid #C9C8CA;
    border-radius: 50%;
    &:after {
      content: "";
      position: absolute;
      display: none;
      left: 8px;
      top: 4px;
      width: 5px;
      height: 10px;
      border: solid rgb(255, 255, 255);
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
    }
  }

  &:hover input ~ .checkmark {
    opacity: 0.8;
  }

}