.calendar {
  th:not(.gap), td {
    cursor: pointer;
  }

  @include media-breakpoint-up(md){
    background: #FFFFFF;
    border: 1px solid #C9C8CA;
    border-radius: 4px;
    text-align: center;
  }
  @media (max-width: 350px){
    zoom: 1.25;
    .calendar-body {
      position: relative;
      top: 2px;
    }
  }

  table {
    display: flex;
  }

  td {
    font-weight: bold;
    font-size: 18px;
    color: $muted-color;
    padding: 0.25rem 1rem;
    @include media-breakpoint-down(md) {
      padding: 0.25rem 0rem;
    }
  }

  .gap {
    height: calc(5rem + -2px);
  }

  .price-row {
    &:not(.view){
      display: none;
    }
    td:not(.view) {
      display: none;
    }
    .price {
      &-1 {color: rgba(0, 0, 0, 0.1)}
      &-2 {color: rgba(0, 0, 0, 0.2)}
      &-3 {color: rgba(0, 0, 0, 0.4)}
      &-4 {color: $main-color}
      &-q {
        color: $main-color;
        font-weight: bold;
        font-size: 14px;
      }
      &-active, &-hovered-exactly {
        position: relative;
        span {
          padding: 0.4rem 1.2rem;
          color: white;
          background: linear-gradient(154.59deg, #EF226C 0%, #F9B916 100%),#fff;
          border-radius: 32px;
          position: absolute;
          top: -10%;
          left: -10%;
          width: 120%;
          @include media-breakpoint-down(md){
            padding: 0.4rem .2rem;
          }
        }
        &.price-hovered-exactly:not(.price-active) {
          span {
            opacity: 0.5;
          }
        }
      }
    }
  }


  &-date {
    position: relative;
    th {
      padding: 16px 0.1rem;
      padding-bottom: 2rem;
      position: relative;
    }
    p {
      font-weight: bold;
      font-size: 14px;
      margin-bottom: 0;
    }
    &-num {
      color: $second-color;
    }
    &-wd {
      color: $muted-color;
    }
    &-departure {
      visibility: hidden;
      position: absolute;
      bottom: 8px;
      width: 100%;
      font-weight: 500 !important;
      font-size: 12px !important;
      color: $muted-color;
      text-align: center;
      padding: 0 0.75rem;
      @include media-breakpoint-down(md){
        padding: 0;
        text-align: center;
        bottom: -8px;
        z-index: 4;
        background: #F6F5F7;
        width: 98%;
      }
    }
    &-arrival {
      visibility: hidden;
      font-size: 12px !important;
      color: $muted-color;
      position: absolute;
      right: 0;
      margin-right: 1.5rem;
      margin-top: 0.15rem;
      @include media-breakpoint-down(md){
        font-size: 0 !important;
        z-index: 2;
        width: 20px;
        height: 100%;
        top: 0;
        right: -11px;
        margin: 0;
        padding: 4px;
        background: #F6F5F7;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px;
        display: flex !important;
      }
      img {
        transform: matrix(-0.71, 0.71, 0.71, 0.71, 0, 0);
      }
    }
  }

  &-side {
    width: 20%;
    position: relative;
    @include media-breakpoint-down(md){
      width: 30%;
    }
    .gap-mobile {
      display: none;
      @include media-breakpoint-down(md){
        display: block;
        height: 1.5rem;
      }
    }
    .calendar-date {
      width: 100%;
      display: block;

      @include media-breakpoint-down(md){
        th:not(.gap):not(.calendar-control){
          border-right: 1px solid #C9C8CA;
        }
        .entry.view {
          padding-top: 1.15rem;
          & ~ .entry {
            padding-top: 0.35rem;
          }
        }
      }
    }
    th {
      display: block;
      text-align: left;
      padding: 0.35rem 16px;
      p {
        display: inline;
      }
    }
    .entry:not(.view) {
      display: none;
    }
  }

  &-body {
    width: 80%;
    @include media-breakpoint-down(md){
      width: 70%;
      overflow-x: scroll;
      border-bottom: 1px solid #C9C8CA;
      tr {
        position: relative;
      }
      .first-row td {
        border-top: 1px solid #C9C8CA;
        padding-top: 1rem;
      }
    }
    tr {
      display: flex;
      width: 100%;
      @include media-breakpoint-down(md){
        width: 230%;
      }
      td, th {
        width: calc(100% / 9 - 6rem / 9);
        @include media-breakpoint-down(md) {
          width: 25%;
          text-align: center;
        }
      }
    }
    .entry:not(.view) {
      display: none;
    }
  }

  &-control {
    display: block !important;
    position: absolute !important;
    z-index: 2;
    a {
      background: #F6F5F7;
      box-shadow: 0px 8px 64px rgba(0, 0, 0, 0.08);
      border-radius: 4px;
      width: 24px;
      height: 24px;
      display: block;
      text-align: center;
    }

    &.up {
      top: 3rem;
      left: 10%;
      transform: rotate(180deg);
      @include media-breakpoint-down(md) {
        left: 15%;
      }
    }
    &.down {
      left: 10%;
      @include media-breakpoint-down(md) {
        left: 15%;
      }
    }
    &.left {
      transform: rotate(90deg);
      left: -6rem;
      top: 1.4rem;
      @include media-breakpoint-down(md) {
        top: 0;
        left: 0;
      }
    }
    &.right {
      transform: rotate(270deg);
      right: 0.5rem;
      top: -2.5rem;
      @include media-breakpoint-down(md) {
        left: 2rem;
        top: 0;
      }
    }

    &.desktop, &.mobile {
      display: none !important;
    }
    @include media-breakpoint-up(md){
      &.desktop {
        display: block !important;
      }
    }
    @include media-breakpoint-down(md){
      &.mobile {
        display: block !important;
        width: 56px;
      }
    }
  }

  .hovered {
    background: rgba(246, 245, 247, 0.5);
    .calendar-date-departure, .calendar-date-arrival {
      visibility: visible;
      opacity: .5;
    }
  }

  .active {
    background: #F6F5F7;
    .calendar-date-departure, .calendar-date-arrival {
      visibility: visible;
      opacity: 1;
    }
  }

  .last-row {
    td {
      padding-bottom: 3rem;
      @include media-breakpoint-down(md){
        padding-bottom: 2rem;
      }
    }
  }
}