.currency {
  font-weight: 500;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
}
.tab-card .currency {
  margin-top: 9px;
}

.info-badge {
  position: relative;
  cursor: pointer;
  &-down {
    display: none;
    position: absolute;
    right: -25px;
    top: 25px;
    background: white;
    padding: 4px 8px;
    font-size: 0.8rem;
    border: 1px solid #ccc;
    z-index: 99;
    width: 150px;
    text-align: center;
  }
  &:hover {
    .info-badge-down {
      display: block;
    }
  }
}