/**
 * Text Alignment
 */

@if $use-text-alignment {

  .text-left {
    text-align: left;
  }
  .text-center {
    text-align: center;
  }
  .text-right {
    text-align: right;
  }
  .text-justify {
    text-align: justify;
  }

  @include media-breakpoint-up(md){
    .text-md-left {
      text-align: left;
    }
    .text-md-center {
      text-align: center;
    }
    .text-md-right {
      text-align: right;
    }
    .text-md-justify {
      text-align: justify;
    }
  }

}
