.loader-screen {
  display: none;
  &-card {
    &-title {
      font-size: 32px;
      font-weight: 600;
      img {
        width: 32px;
      }
      @include media-breakpoint-down(md){
        font-size: 24px;
        img {
          width: 24px;
        }
      }
    }
    &-text, &-weather {
      background: #F6F5F7;
      border-radius: 4px;
    }
    &-text {
      display: flex;
      align-items: center;
      p {
        font-size: 18px;
        line-height: normal;
        margin: 0;
      }
      @include media-breakpoint-down(md){
        .icon-placeholder {
          width: 100px;
        }
      }
    }
    &-weather {
      font-size: 14px;
      font-weight: 600;
      &-bold {
        font-weight: bold;
        font-size: 18px;
      }
      .sub {
        font-size: 9px;
        font-weight: 300;
        display: block;
      }
      img {
        width: 14px;
      }
    }

  }
}