.tour, .hotel-wide {
  border-radius: 2px;
  position: relative;
  width: 100%;
  &-bg {
    width: 100%;
    @include media-breakpoint-down(md){
      height: 284px;
      object-fit: cover;
    }
  }
  &-title {
    position: absolute;
    left: 20px;
    top: 20px;
    @include media-breakpoint-down(md) {
      >p{
        display: flex;
        flex-flow: wrap;
        span {
          line-height: normal;
        }
      }
      left: 0;
      padding: 0 20px;
    }
    &-name {
      font-weight: bold;
      font-size: 24px;
      color: #fff;
      text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 0.5);
      margin-right: 11px;
      line-height: normal;
    }
    &-hotel {
      font-family: Oswald, sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      color: #fff;
      text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 0.5);
      margin-right: 11px;
      @include media-breakpoint-down(md){
        display: block;
        margin-top: 10px;
        order: 2;
      }
    }
    &-stars {
      @include media-breakpoint-down(md){
        order: 1;
        position: relative;
        top: 6px;
      }
    }
    &-date, &-destination {
      font-weight: bold;
      font-size: 14px;
      color: #fff;
      text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 1);
    }
  }
  &-bookmark {
    @include bookmark;
  }

  &-top {
    position: relative;
  }
  &-bottom {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px 15px;
    @include media-breakpoint-down(md){
      flex-flow: column-reverse;
      align-items: initial;
      padding: 10px 8px;
    }
  }
  &-details {
    margin-left: 10px;
    font-weight: 500;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8);
    @include media-breakpoint-down(md){
      margin-bottom: 1rem;
    }
  }
  &-order {
    &-link {
      .btn {
        padding: 11px
      }
      &:hover {
        text-decoration: none;
      }
    }
    &-price {
      font-weight: bold;
      font-size: 18px;
      color: #F2584D;
      position: relative;
      top: 1px;
      &:hover {
        color: #F2584D;
      }
    }
  }

  &-quicklink {
    color: $main-color;
    font-weight: bold;
    font-size: 14px;
    order: 3;
    position: absolute;
    right: 15px;
    @include media-breakpoint-down(md) {
      bottom: 20%;
    }
  }

  &-status {
    color: gray;
    &-waiting {
      color: #F97F2A;
    }
    &-success {
      color: #13C13A;
    }
  }

  .posa {
    @include posa;
  }

}