.block-about {
  height: 740px;
  background: url(/assets/img/about.jpg) no-repeat;
  background-size: cover;
  display: flex;
  padding-top: 5rem;
  padding-bottom: 5rem;
  @include media-breakpoint-down(md){
    background-size: auto;
    background-position: center;
  }
  &-heading {
    font-weight: bold;
    font-size: 48px;
    color: #000;
    margin-bottom: 3rem;
    @include media-breakpoint-down(md){
      font-size: 32px;
    }
  }
  p {
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;
    color: #000;
    margin-bottom: 3rem;
  }
}