.slider-country {
  .swiper-container {
    width: 100%;
    height: 700px;
    @include media-breakpoint-down(md){
      height: auto;
      display: block;
      .swiper-wrapper {
        display: block;
      }
    }
    margin-left: auto;
    margin-right: auto;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    height: 45% !important;
    margin-top: 0 !important;
    max-height: 350px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    @include media-breakpoint-down(md){
      height: auto !important;
      margin-top: 1rem !important;
      display: block;
    }
  }
}

.hotel-big-slider {

  .swiper-container {
    width: 100%;
    height: 665px;
    @include media-breakpoint-down(md){
      height: 180px;
    }
    margin-left: auto;
    margin-right: auto;
  }
  .swiper-slide {
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
  }
  .gallery-thumbs {
    height: 20%;
    box-sizing: border-box;
    padding: 10px 0;
    .swiper-slide {
      width: 10%;
      @include media-breakpoint-down(md){
        width: 25%;
      }
      height: 80px;
      opacity: 0.4;
      &-thumb-active {
        opacity: 1;
      }
    }
  }

}

.gallery-casual {
  &.swiper-container {
    width: 100%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
  }
  .swiper-slide {
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
  }
}

.swiper-pagination {
  &-bullet {
    background: #ECEBED !important;
    outline: none;
    opacity: 1 !important;
    &-active {
      background: linear-gradient(0deg, #F2584D, #F2584D), #FF6900 !important;
    }
  }

}

