.rich-search {
  &:not(.advanced-search){
    .advanced { display: none }
  }

  &.advanced-search {
    .simple { display: none }
  }
}

.advanced-search-toggle {
  font-size: 12px;
  color: $second-color;
  font-weight: 600;
  img {
    position: relative;
    top: -1.5px;
  }
  &:hover {
    text-decoration: none;
    color: $second-color;
  }
  &-mobile {
    font-size: 18px;
  }
}

.clear-form {
  margin-left: 1rem;
  font-weight: 500;
  font-size: 12px;
  color: $main-color;
  img {
    position: relative;
    top: -1px;
  }
  &:hover {
    color: $main-color;
    text-decoration: none;
  }
}

.toggle-switch {
  float: right;
  border: 1px solid #C9C8CA;
  border-radius: 16px;
  width: 48px;
  height: 28px;
  position: relative;
  &:after {
    content: ' ';
    position: absolute;
    background: #C9C8CA;
    border-radius: 180px;
    width: 22px;
    height: 22px;
    top: 2px;
    left: 2px;
  }
}
.advanced-search .toggle-switch {
  &:after {
    background: $main-color;
    left: auto;
    right: 2px;
  }
}

.search-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.ticket-regular-filters {
  position: fixed;
  width: 100vw;
  left: 0;
  bottom: 0;
  padding: 12px 0;
  background: #ECEBED;
  z-index: 50;
  &-inner {
    height: 100%;
    display: flex;
    align-items: center;
  }
  &-row {
    @include media-breakpoint-down(md){
      overflow-x: scroll;
      >.row {
       width: max-content;
        .row {
          margin-left: 0;
        }
        .col-auto {
          padding-right: 0;
        }
      }
    }
  }
  &-entry {
    background: #FFFFFF;
    border: 1px solid #C9C8CA;
    border-radius: 4px;
    width: 100%;
    display: flex;
    height: 48px;
    align-items: center;
    padding: 0 10px;
    @include media-breakpoint-down(md){
      padding-right: 3rem;
    }
    color: $second-color;
    font-weight: 500;
    font-size: 14px;
    &:hover {
      color: $second-color;
      text-decoration: none;
    }
    &:after {
      content: ' ';
      position: absolute;
      background: url(/assets/img/icons/dropdown.svg) no-repeat;
      width: 16px;
      height: 16px;
      right: 24px;
      @include media-breakpoint-down(md){
        right: 12px;
      }
    }
    &.active {
      border-color: $main-color;
      &:after {
        transform: rotate(180deg);
      }
    }
  }
  &-details {
    display: none;
    margin-bottom: 12px;
    &.active {
      display: block;
    }
    &-close {
      position: absolute;
      background: url(/assets/img/icons/close.svg) no-repeat;
      width: 16px;
      height: 16px;
      right: 15px;
      cursor: pointer;
      background-size: cover;
      opacity: .2;
    }
  }
  &-title {
    span {
      color: $second-color;
      font-weight: bold;
      font-size: 18px;
    }
    .checkbox {
      display: inline-block;
      font-weight: 500;
      font-size: 12px;
      margin-left: 16px;
      position: relative;
      top: -2px;
    }
  }
  &-body {
    margin-top: 1rem;
    &:after {
      content: ' ';
      display: table;
      clear: both;
    }
  }
}