.tripadvisor {
  &-title {
    font-weight: bold;
    font-size: 18px;
    @include media-breakpoint-down(md){
      font-size: 24px;
      margin-bottom: 3rem;
    }
    color: $second-color;
  }
  &-card {
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    position: relative;
    padding: 1rem;

    &-logo {
      position: absolute;
      top: -13px;
      right: 10px;
      padding: 0 0.5rem;
      background: white;
    }
    &-content {
      font-weight: 500;
      font-size: 12px;
      color: $second-color;
    }
  }
  &-link {
    font-weight: bold;
    font-size: 14px;
    color: $main-color;
  }
}