.country {
  background: #FFFFFF;
  border: 1px solid #E5E5E5;
  border-radius: 2px;
  position: relative;
  width: 100%;
  &-bg {
    width: 100%;
  }
  &-name {
    font-weight: bold;
    font-size: 24px;
    color: #fff;
    text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 20px;
    top: 10px;
    line-height: normal;
  }
  &-bookmark {
    @include bookmark;
  }
  &-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
  }
  &-cities {
    font-size: 14px;
    color: #000000;
  }
  &-price {
    font-weight: bold;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8);
  }
  &-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }
}