/**
 * Footer
 */

.footer {
  &-actions {
    background: #ECEBED;
    @include media-breakpoint-down(md) {
      text-align: center;
    }
  }
  &-title {
    margin-bottom: 32px;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.8);
    @include media-breakpoint-down(md){
      font-size: 32px;
      line-height: 120%;
    }
  }
  &-content {
    font-weight: 500;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8);
  }
  &-menu {
    list-style: none;
    padding: 0;
    a {
      font-weight: 500;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.8);
      &:hover {
        text-decoration: none;
      }
    }
  }
  &-social {
    margin-top: 32px;
    @include media-breakpoint-up(md){
      .social-links {
        justify-content: initial;
      }
    }
  }
  &-copyright {
    background: rgba(201, 200, 202, 0.5);
    .info {
      font-weight: 500;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.5);
      a {
        color: rgba(0, 0, 0, 0.75);
      }
    }
    .copy {
      @include media-breakpoint-up(md){
        text-align: right;
        font-weight: 500;
        font-size: 14px;
        color: rgba(0, 0, 0, 0.5);
      }
    }
  }
}
