.block-blog {
  &.bg-gray {
    background: #F6F5F7;
  }
  &-post {
    &-date {
      position: absolute;
      top: 25px;
      left: 40px;
      .num {
        font-weight: bold;
        font-size: 48px;
        color: #fff;
        text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 1);
      }
      .mnt {
        font-weight: 500;
        font-size: 14px;
        color: #fff;
        text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 1);
      }
    }
    &-img {
      width: 100%;
    }
    &-title {
      margin-top: 16px;
      font-weight: bold;
      font-size: 24px;
      line-height: 140%;
      color: rgba(0, 0, 0, 0.8);
      padding-left: 24px;
    }
    &-desc {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: rgba(0, 0, 0, 0.8);
      padding-left: 24px;
    }
    &-more {
      padding-left: 24px;
      &:hover {
        text-decoration: none;
      }
      &-btn.btn.btn-main {
        padding: 5px;
        font-size: 12px;
        width: 34px;
      }
      &-text {
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        color: #F2584D;
        position: relative;
        top: 3px;
        margin-left: 5px;
      }
    }
  }

  .posa {
    @include posa;
  }
}