.lowprice {
  &-country {
    display: flex;
    margin-bottom: 2rem;
    @include media-breakpoint-down(md){
      overflow-x: scroll;
    }

    span {
      font-weight: 500;
      font-size: 14px;
      color: $second-color;
      margin-right: 16px;
      white-space: pre;
    }
    a {
      font-weight: bold;
      font-size: 14px;
      border-bottom: 2px solid transparent;
      color: $main-color;
      margin-right: 16px;

      &.active {
        color: $second-color;
      }

      &.active, &:hover {
        border-color: $main-color;
        text-decoration: none;
      }
    }

    @include media-breakpoint-down(md) {
      padding: 0 1rem;
    }
  }

  &-chart {
    &-wrapper {
      height: 180px;
      position: relative;
    }
    &-grid {
      height: 100%;
      width: 100%;
      position: absolute;
      padding-top: 1.5rem;
      padding-bottom: 4rem;
      z-index: 4;
      pointer-events: none;
      &-wrapper {
        position: relative;
        height: 100%;
      }
    }
    &-gridline {
      position: absolute;
      display: flex;
      width: 100%;
      white-space: pre;
      align-items: center;
      pointer-events: none;
      height: 0;
      &:after {
        content: ' ';
        background: rgba(201, 200, 202, 0.5);
        height: 1px;
        width: 100%;
      }
      span {
        font-weight: 500;
        font-size: 12px;
        color: $muted-color;
        padding-right: 3px;
      }
    }
    &-body {
      position: relative;
      width: 100%;
      height: 100%;
      overflow-x: hidden;
      overflow-y: visible;
      @include media-breakpoint-down(md){
        overflow-x: scroll;
      }
    }
    &-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 3;
      pointer-events: none;
      &-left, &-right {
        background: linear-gradient(269.19deg, #FFFFFF 2.99%, rgba(255, 255, 255, 0) 99.64%);
        width: 2rem;
        height: 100%;
        position: absolute;
      }
      &-left {
        transform: rotate(-180deg);
      }
      &-right {
        right: 0;
      }
    }
    &-data {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      flex-wrap: wrap;
      overflow-x: hidden;
      overflow-y: hidden;
      cursor: move;
      width: max-content;
      height: 100%;
      padding-left: 5rem;
      padding-bottom: 4rem;
      padding-top: 1.5rem;
      &-entry {
        position: relative;
        width: 24px;
        bottom: 0;
        background: rgba(0, 0, 0, 0.1);
        border-radius: 2px 2px 0px 0px;
        margin-right: 8px;
        cursor: pointer;
        transition: background 0.3s;
        .price {
          position: absolute;
          top: -1.5rem;
          font-weight: bold;
          font-size: 14px;
          color: $main-color;
          white-space: pre;
          pointer-events: none;
          opacity: 0;
          transition: opacity 0.3s;
          left: -1rem;
        }
        .day {
          position: absolute;
          bottom: -3rem;
          text-align: center;
          font-weight: 500;
          font-size: 12px;
          color: $muted-color;
          transition: color 0.3s;
        }
        &.active, &:hover {
          .price {
            opacity: 1;
          }
        }
        &.active {
          background: $main-color;
          .day {
            font-weight: 600;
            color: $main-color;
          }
        }
      }
      &-month {
        color: rgba(0, 0, 0, 0.2);
        font-weight: bold;
        font-size: 12px;
        position: absolute;
        bottom: -20px;
        &:not(.active){
          transform: translateX(0px) !important;
        }
      }
    }
  }

  .arrow-right {
    transform: rotate(180deg);
  }

  &-controls {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 5;
    top: -1.15rem;
    pointer-events: none;
    div {
      pointer-events: all;
    }
    @include media-breakpoint-down(md) {
      padding: 0 1rem;
    }
  }
}