.input {
  height: 48px;
  width: 100%;
  border-radius: 4px;
  outline: none;
  border: 2px solid #C9C8CA;
  padding-left: 10px;
  &-bg {
    background: #fff;
    border-radius: 4px;
  }
  &.input-search {
    background: url(/assets/img/icons/search.svg) no-repeat scroll 15px 12px;
    padding-left: 50px;
  }
  &.input-date, &.input-date-range {
    background: url(/assets/img/icons/date-sm.svg); // preload hack
    background: url(/assets/img/icons/date.svg) no-repeat scroll 15px 12px;
    padding-left: 50px;
    &.date-chosen {
      background: url(/assets/img/icons/date-sm.svg) no-repeat scroll 95% 15px;
      padding-left: 10px;
      padding-right: 20px;
      font-weight: bold;
    }
  }

  &-price {
    padding: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    max-width: 100%;
    span {
      font-weight: 500;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.2);
    }
    input {
      border: none;
      outline: none;
      text-align: center;
      padding: 0 3px;
      color: rgba(0, 0, 0, 0.8);
      font-size: 14px;
      font-weight: bold;
    }
    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    input[type=number] {
      -moz-appearance: textfield;
      max-width: 30%;
    }
  }

  &-error {
    .input {
      border-color: #FF1100;
    }
    &-txt {
      display: none;
    }

    .input-error-txt {
      display: block;
      position: relative;
      font-weight: 500;
      font-size: 12px;
      color: #FF1100;
      top: -10px;
      left: 15px;
      background: white;
      width: fit-content;
      padding: 0 8px;
    }
  }

  &-textarea {
    height: auto;
    padding: 10px;
  }
  
  &-text, &-textarea {
    &-container {
      position: relative;
    }
  }
}

.quantity {
  position: relative;
  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  input[type=number] {
    -moz-appearance: textfield;
  }
  input {
    height: 48px;
    line-height: 1.65;
    float: left;
    display: block;
    padding: 0 20px;
    text-align: center;
    color: rgba(0, 0, 0, 0.8);
    font-weight: bold;
    &:focus {
      outline: 0;
    }
  }
  &-nav {
    float: left;
    position: relative;
    height: 42px;
  }
  &-button {
    position: absolute;
    cursor: pointer;
    width: 24px;
    height: 24px;
    text-align: center;
    color: rgba(0, 0, 0, 0.8);
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    background: #F6F5F7;
    box-shadow: 0px 8px 64px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    top: 12px;
    &.quantity-up {
      right: 8px;
    }
    &.quantity-down {
      left: 8px;
    }
  }
}