.cabinet {
  &-counter {
    font-size: 12px;
    font-weight: bold;
    color: $main-color;
    position: relative;
    top: -5px;
  }
  &-menu {
    list-style: none;
    padding: 0;
    display: inline-flex;
    margin-left: 1rem;
    @include media-breakpoint-down(md){
      display: flex;
      margin-top: .5rem;
      margin-left: 0;
      overflow-x: auto;
    }
    li {
      margin-right: 10px;
      white-space: pre;
      padding-bottom: 4px;
      border-bottom: 1px solid transparent;
      font-weight: bold;
      font-size: 14px;

      a {
        color: $main-color;
        &:hover {
          color: $second-color;
          text-decoration: none;
        }
      }

      &.active {
        border-bottom-color: $main-color;
        a {
          color: $second-color;
        }
      }
    }
  }

  &-favorites, &-prices {
    >div {
      display: none;
      &.show {
        display: block;
      }
    }
  }

  &-bonus {
    &-question {
      font-weight: bold;
      font-size: 18px;
      line-height: normal;
    }
    &-content {
      font-size: 1rem;
      line-height: normal;
    }
    &-counter {
      width: 144px;
      height: 121px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border: 1px solid $main-color;
      border-radius: 2px;
      margin: auto;
      span {
        display: block;
        line-height: normal;
      }
      &-title {
        font-size: 12px;
        color: rgba(0, 0, 0, 0.5);
      }
      &-points {
        font-weight: bold;
        font-size: 48px;
        color: $main-color;
      }
      &-sub {
        font-weight: bold;
        font-size: 18px;
        color: $main-color;
      }
    }
  }
}

