.insurance {
  p {
    font-size: 1rem;
    line-height: normal;
  }
  h1 {
    line-height: normal;
  }
  &-faq {
    &-link {
      color: $main-color;
      margin-bottom: 1rem;
      display: block;
      &:after {
        content: ' ';
        width: 16px;
        height: 13px;
        margin-left: 10px;
        background: url(/assets/img/icons/dropdown.svg) no-repeat;
        display: inline-block;
      }
    }
    &-answer {
      display: none;
      &.active {
        display: block;
      }
    }
  }
}