.order-raise {
  @include media-breakpoint-up(md){
    margin-top: -6.5rem;
  }
  .card {
    &-header {
      background: white;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

.order {
  &-card {
    &-title {
      font-weight: bold;
      font-size: 14px;
      color: $second-color;
      margin-bottom: 0;
    }
    &-link {
      font-weight: 500;
      font-size: 12px;
      color: $main-color;
      &:hover {
        color: $main-color;
        text-decoration: none;
      }
    }

    &-ticket {
      margin-bottom: 1rem;
      &-logo {
        display: flex;
        align-items: center;
        span {
          color: $main-color;
          font-weight: bold;
          margin-left: 4px;
          @include media-breakpoint-down(md) {
            font-size: 14px;
          }
        }
      }
      &-air {
        font-weight: 500;
        font-size: 12px;
        color: $muted-color;
      }
      &-block {
        .sub {
          font-size: 14px;
          color: $muted-color;
        }
        .name {
          font-weight: bold;
          font-size: 24px;
          color: $second-color;
        }
      }
      &-mid {
        margin-top: -1rem;
      }
      &-way {
        display: flex;
        padding-left: 2rem;
        align-items: center;
        p {
          padding-left: 1rem;
          margin-bottom: 0;
          font-weight: 500;
          font-size: 12px;
          color: $muted-color;
        }
      }
      &-transfer {
        &-badge {
          font-weight: 500;
          font-size: 9px;
          color: #333;
          padding: 0 4px;
          border: 1px solid #333333;
          border-radius: 4px;
          margin-right: 10px;
        }
        &-time {
          font-weight: 500;
          font-size: 12px;
          color: $muted-color;
          white-space: pre;
          margin-right: 10px;
        }
      }
      &-rule {
        display: flex;
        hr {
          width: 100%;
          border-color: #C9C8CA;
          border-top: 0;
        }
      }
    }

    &-total {
      &-title {
        font-weight: bold;
        font-size: 18px;
        color: $second-color;
      }
      &-sub {
        font-weight: 500;
        font-size: 18px;
        color: $second-color;
      }
      &-price {
        font-weight: bold;
        font-size: 24px;
        color: $main-color;
      }
      &-price-sub {
        font-weight: bold;
        font-size: 18px;
        color: $main-color;
      }
    }
  }

  &-add {
    font-weight: bold;
    font-size: 14px;
    color: $second-color;
    display: none;
    align-items: center;
    &:hover {
      text-decoration: none;
    }
    span {
      margin-left: 8px;
    }
    &-container {
      @include media-breakpoint-up(md) {
        padding-top: 0.5rem;
      }
    }
  }
}

.passenger-copy {
  display: none;
}
.passengers-list:not(.max)>div:last-of-type {
  .order-add {
    display: flex;
  }
}