.blog-single {
  &-promo {
    border: 1px solid rgba(0, 0, 0, 0.2);
    width: 300px;
    height: 600px;
  }
  &-place {
    font-weight: bold;
    font-size: 24px;
    color: $muted-color;
  }
  &-actions {
    .bookmark-link {
      img {
        position: relative;
        top: -2px;
      }
      .active {
        display: none;
      }
      &.active {
        .active {
          display: initial;
        }
        .inactive {
          display: none;
        }
      }
    }
    a {
      font-weight: bold;
      font-size: 14px;
      color: $second-color;
      @include media-breakpoint-down(md){
        font-size: 0;
        margin: 0!important;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }

  &-content {
    p {
      font-size: 1rem;
      color: $second-color;
      line-height: 1.5rem;
    }
    .heading {
      font-size: 1.5rem;
      font-weight: 600;
      margin: 2rem 0;
    }
    .subheading {
      font-size: 1.25rem;
      font-weight: 600;
    }
    img {
      width: 100%;
      max-width: 100%;
    }
  }
}