.about {
  &-extra {
    &-circle {
      border: 3px solid $main-color;
      border-radius: 360px;
      width: 150px;
      height: 150px;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 80px;
      }
    }
    &-text {

    }
  }
  &-feedback {

  }
  &-faq {
    &-link {
      color: $main-color;
      margin-bottom: 1rem;
      display: block;
      &:after {
        content: ' ';
        width: 16px;
        height: 13px;
        margin-left: 10px;
        background: url(/assets/img/icons/dropdown.svg) no-repeat;
        display: inline-block;
      }
    }
    &-answer {
      display: none;
      &.active {
        display: block;
      }
    }
  }
}