.bus-picker {
  &-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include media-breakpoint-down(md){
      flex-direction: column;
      h1 {order:2}
      &-time {order:1}
    }
  }
  &-time {
    font-size: 12px;
    color: $main-color;
    @include media-breakpoint-down(md){
      margin-bottom: 2rem;
    }

    img {
      position: relative;
      bottom: 2px;
    }
    &:hover {
      color: $main-color;
      text-decoration: none;
    }
  }

  &-raise .card-header {
    @include media-breakpoint-up(md){
      justify-content: center !important;
    }
  }
  &-city {
    color: $second-color;
    font-weight: bold;
    font-size: 18px;
    @include media-breakpoint-up(md){
      position: absolute;
      left: 1.25rem;
    }
  }
  &-space {
    color: $second-color;
    font-weight: bold;
    font-size: 14px;
    @include media-breakpoint-down(md){
      font-weight: normal;
      font-size: 12px;
    }
  }

  &-info {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 8px;
  }

  &-wrapper {
    @include media-breakpoint-up(md){
      display: flex;
      align-items: center;
      justify-content: center;
      height: 300px;
    }
  }
  &-ui {
    padding: 1.5rem;
    @include media-breakpoint-up(md){
      transform: rotate(-90deg);
      width: 300px;
    }
  }
  &-row {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-column-gap: 8px;
    margin-bottom: 8px;
  }
  &-driver {
    width: 100%;
  }
  &-place {
    border: 1px solid;
    border-color: $main-color;
    border-radius: 4px;
    width: 100%;
    padding-top: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    span {
      position: absolute;
      font-weight: bold;
      font-size: 14px;
      color: $second-color;
      top: 25%;
      @include media-breakpoint-up(md){
        transform: rotate(90deg);
      }
    }

    &.active {
      background: $main-color;
      span {
        color: white;
      }
    }
    &.inactive {
      background: #ECEBED;
      border: none;
      cursor: default;
      span {
        color: white;
      }
    }
  }

}